import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import NotFound from '../views/NotFound.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/buscar',
    name: 'buscar',
    component: () => import(/* webpackChunkName: "buscar" */ '../views/BuscarView.vue')
  },
  {
    path: '/peleador/:id',
    name: 'peleador',
    props: true,
    component: () => import(/* webpackChunkName: "buscar" */ '../views/Peleador.vue')
  },
  {
    path: '*',
    component: NotFound
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
